import { filters } from '..';
export const saveToLocalStorage = () => {
    const filtersValue = JSON.stringify(filters);
    window.localStorage.setItem('filters', filtersValue);
};
export const getFromLocalStorage = () => {
    const data = window.localStorage.getItem('filters');
    const filtersValue = JSON.parse(data);
    for (const key in filtersValue) {
        filters[key] = filtersValue[key];
    }
};
