import { Link } from '../../shared/link';
import { Wrapper } from '../../shared/wrapper';
import { BaseComponent } from '../baseComponent';
import { Cart } from '../cart/cart';
import './header.scss';
export class Header extends BaseComponent {
    links = ['home', 'about', 'products'];
    constructor() {
        super('header', ['header']);
    }
    draw = () => {
        const wrapper = new Wrapper(['header__wrapper']);
        const logo = new Link(['header__logo', 'logo'], 'MyKEA', '#');
        const nav = document.createElement('nav');
        nav.classList.add('header__nav', 'nav');
        this.links.forEach((link) => {
            const navLink = new Link(['nav__link'], link, '#');
            nav.append(navLink.node);
        });
        const cart = new Cart(['header__cart']);
        cart.draw('Cart');
        wrapper.node.append(logo.node, nav, cart.node);
        this.node.append(wrapper.node);
    };
}
