import { Cards } from './card/cards';
import { CategoryFilter } from './filters/categoryFilter';
import { ClearButton } from './filters/clearButton';
import { ColorsFilter } from './filters/colorsFilter';
import { CompanyFilter } from './filters/companyFilter';
import { PopularFilter } from './filters/popularFilter';
import { PriceFilter } from './filters/priceFilter';
import { QueryFilter } from './filters/queryFilter';
import { YearFilter } from './filters/yearFilter';
import { Sort } from './sort/sort';
export class View {
    draw = (controller, rootElement) => {
        const sort = new Sort();
        sort.draw();
        const filtersContainer = document.querySelector('.products__filters');
        filtersContainer.innerHTML = '';
        const queryFilter = new QueryFilter();
        const categoryFilter = new CategoryFilter();
        const companyFilter = new CompanyFilter();
        const colorsFilter = new ColorsFilter();
        const priceFilter = new PriceFilter();
        const yearFilter = new YearFilter();
        const popularFilter = new PopularFilter();
        const clearButton = new ClearButton();
        filtersContainer.append(queryFilter.node, categoryFilter.node, companyFilter.node, colorsFilter.node, priceFilter.node, yearFilter.node, popularFilter.node, clearButton.node);
        const cards = new Cards();
        controller.getCards((data) => cards.draw(data, controller.filters));
        rootElement.addEventListener('input', () => {
            controller.getCards((data) => cards.draw(data, controller.filters));
        });
        document.addEventListener('filter', () => {
            controller.getCards((data) => cards.draw(data, controller.filters));
        });
    };
}
