import { filters } from '../../../index';
import { saveToLocalStorage } from '../../../util/util';
import { BaseComponent } from '../baseComponent';
export class SortFilter extends BaseComponent {
    values = [
        {
            value: 'price-lowest',
            desc: 'Price (Lowest)',
        },
        {
            value: 'price-highest',
            desc: 'Price (Highest)',
        },
        {
            value: 'desc-a',
            desc: 'desc (A-Z)',
        },
        {
            value: 'desc-z',
            desc: 'desc (Z-A)',
        },
        {
            value: 'newer',
            desc: 'first newer',
        },
        {
            value: 'older',
            desc: 'first older',
        },
    ];
    constructor() {
        super('select', ['sort__list']);
        this.node.id = 'sort';
        this.node.setAttribute('name', 'sort');
        this.values.forEach(({ value, desc }) => {
            const opt = document.createElement('option');
            opt.value = value;
            opt.textContent = desc;
            this.node.append(opt);
        });
        this.node.value = filters.sort;
        this.node.addEventListener('input', this.onChange);
    }
    onChange = (e) => {
        const value = e.target.value;
        filters.sort = value;
        saveToLocalStorage();
    };
}
