import { filters } from '../../../index';
import { saveToLocalStorage } from '../../../util/util';
import { BaseComponent } from '../baseComponent';
export class QueryFilter extends BaseComponent {
    constructor() {
        super('div', ['filters__query', 'query']);
        const input = document.createElement('input');
        input.classList.add('query__input');
        input.type = 'text';
        input.id = 'query';
        input.placeholder = 'Search';
        input.setAttribute('name', 'query');
        input.autocomplete = 'off';
        input.value = filters.search;
        const btn = document.createElement('button');
        btn.classList.add('query__btn', 'btn', 'visually-hidden');
        if (input.value.length > 0) {
            btn.classList.remove('visually-hidden');
        }
        this.node.append(input, btn);
        btn.addEventListener('click', (e) => this.clearQuery(e, input));
        input.addEventListener('input', (e) => this.onChange(e, btn));
    }
    clearQuery = (e, query) => {
        e.target.classList.add('visually-hidden');
        query.value = '';
        query.dispatchEvent(new Event('input', { bubbles: true }));
    };
    onChange = (e, btn) => {
        e.preventDefault();
        const value = e.target.value;
        filters.search = value;
        saveToLocalStorage();
        if (value.length > 0) {
            btn.classList.remove('visually-hidden');
        }
        else {
            btn.classList.add('visually-hidden');
        }
    };
}
