import { filters } from '../../index';
export class Filters {
    data;
    constructor(data) {
        this.data = data;
    }
    getData = () => {
        this.filters();
        return this.data;
    };
    filters = () => {
        this.sortFilter(this.data);
        this.searchFilter(this.data);
        this.categoryFilter(this.data);
        this.companyFilter(this.data);
        this.colorFilter(this.data);
        this.priceFilter(this.data);
        this.yearFilter(this.data);
        this.popularFilter(this.data);
    };
    sortFilter = (items) => {
        switch (filters.sort) {
            case 'price-lowest':
                items.sort((a, b) => (a.price > b.price ? 1 : -1));
                break;
            case 'price-highest':
                items.sort((a, b) => (a.price > b.price ? -1 : 1));
                break;
            case 'desc-a':
                items.sort((a, b) => (a.name > b.name ? 1 : -1));
                break;
            case 'desc-z':
                items.sort((a, b) => (a.name > b.name ? -1 : 1));
                break;
            case 'newer':
                items.sort((a, b) => (a.year > b.year ? 1 : -1));
                break;
            case 'older':
                items.sort((a, b) => (a.year > b.year ? -1 : 1));
                break;
        }
    };
    searchFilter = (items) => {
        this.data = [...items.filter((item) => item.name.includes(filters.search.toLowerCase()))];
    };
    categoryFilter = (items) => {
        this.data = [
            ...items.filter((item) => filters.category === '' ? item : item.category === filters.category),
        ];
    };
    companyFilter = (items) => {
        this.data = [
            ...items.filter((item) => filters.company === '' ? item : item.company === filters.company),
        ];
    };
    colorFilter = (items) => {
        this.data = [
            ...items.filter((item) => {
                if (filters.colors === '') {
                    return item;
                }
                else {
                    for (let i = 0; i < item.colors.length; i++) {
                        if (filters.colors === item.colors[i]) {
                            return item;
                        }
                    }
                }
            }),
        ];
    };
    priceFilter = (items) => {
        const [min, max] = filters.price.split(' ');
        this.data = [
            ...items.filter((item) => Math.round(item.price / 100) >= +min && Math.round(item.price / 100) <= +max),
        ];
    };
    yearFilter = (items) => {
        const [min, max] = filters.year.split(' ');
        this.data = [...items.filter((item) => item.year >= +min && item.year <= +max)];
    };
    popularFilter = (items) => {
        const isPopular = filters.popular === 'true';
        this.data = isPopular ? [...items.filter((item) => item.popular)] : [...items];
    };
}
